
.bg-dark {
  background-color: var(--color-background-nabvar)!important;
}

.navbar-dark .navbar-nav .nav-link:active,
.navbar-dark .navbar-nav .nav-link:current {
  color: var(--color-primary);
}

.navbar-dark .navbar-nav .nav-link {
  color: var(--color-dropdown);
}

.navbar-dark .navbar-nav .nav-link:hover {
  color: var(--color-primary);
}

#navbarHome > ul.navbar-nav.ms-auto > li > a {
  color: var(--color-link);
  cursor: pointer;
}

#navbarHome > ul.navbar-nav.ms-auto > li > a:hover {
  color: var(--color-link-hover);
}

#myDropdown > ul.navbar-nav.ms-auto > li > a {
  color: var(--color-link);
  cursor: pointer;
}

#myDropdown > ul.navbar-nav.ms-auto > li > a:hover {
  color: var(--color-link-hover);
}

.dropdown-menu[data-bs-popper] {
  background: var(--color-background-dropdown) !important;
  box-shadow: var(--color-box-shadow-primary) !important;;
  color: var(--color-dropdown);
}

.dropdown-item {
  color: var(--color-dropdown);
}

.dropdown-item:hover {
  color: var(--color-primary);
  background: var(--color-background-dropdown);
}

.dropdown-item.active {
  color: var(--color-primary);
  background: var(--color-background-dropdown);
}
